body{
  background-image: url("https://ak7.picdn.net/shutterstock/videos/1007908897/thumb/1.jpg");
  background-repeat: repeat;
  background-size: 1300px 750px; 
}

.prayerTimes{
  color:black;
  margin: 50px;
  text-align:center;
  font-family: 'Marvel';
}


table, th, td {
  border: 3px solid rgb(151, 112, 60);
  text-align: center;
  height: 40px;
  margin-left:auto; margin-right:auto;  
  font-size: 25px;
}


.container{
  margin: 0 auto;
  width:310px;
  height:290px;
  background-image: url("http://www.clker.com/cliparts/d/e/2/a/12983844231644038661Windrose_ENG_web.svg.med.png")
}

.directions{
  width:90%;
  height:90%;
  border-radius:50%;
  text-align:center;
  font-size:25px;
  color:#395060;
  z-index:1;
}

.needle{
width:40px;
height:170px;
background-image: url("https://unixtitan.net/images/needle-transparent-compass-6.png");
background-size:600% ;
background-repeat: no-repeat;
background-position-x: -100px;
background-position-y: -40px;
position: relative;
left: 128px;
top : 50px;
z-index:1;
}

p, h4{
  font-size: 23px;
  font-weight: bold;
}

h1{
  font-style: bold;
  font-size: 40px;
}



